import Chat from './Chat.vue'
import Drag from 'components/Drag.vue'
import DragResize from 'components/DragResize.vue'

import isEqual from 'lodash.isequal'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations, mapActions } = createNamespacedHelpers('Chat')
export default {
  name: 'Dialog',
  components: { Chat, DragResize, Drag },
  data() {
    return {
      timerGlobal: null,
      dragging: false
    }
  },
  computed: {
    ...mapGetters(['miniMode', 'data', 'config']),
    dialog: {
      get() {
        return this.$store.getters['Chat/dialog']
      },
      set(isOpen) {
        this.setDialog(isOpen)
      }
    },
    coordinates: {
      get() {
        return this.$store.getters['Chat/coordinates']
      },
      set(coords) {
        this.setCoordinates(coords)
      }
    },
    buttonCoordinates: {
      get() {
        return this.$store.getters['Chat/buttonCoordinates']
      },
      set(coords) {
        if (!isEqual(coords, { ...this.$store.getters['Chat/buttonCoordinates'] })) {
          this.dragging = true
          this.setButtonCoordinates(coords)
        }
      }
    },
    computedMiniMode() {
      return this.$q.platform.is.desktop ? this.miniMode : false
    },
    buttonAnimationClasses() {
      return this.data.amountOfNewMessages ? 'animated flash' : ''
    }
  },
  watch: {
    dialog() {
      if (this.dialog) {
        clearInterval(this.timerGlobal)
      } else {
        this.trackNewMessages()
      }
    }
  },
  methods: {
    ...mapActions(['fetchChatConfig', 'updateAmountOfNewMessages']),
    ...mapMutations(['setDialog', 'setCoordinates', 'setButtonCoordinates']),
    initTimer() {
      this.timerGlobal = setTimeout(this.trackNewMessages, this.config.refreshTimeGlobal)
    },
    trackNewMessages() {
      AppConnector.request({
        module: 'Chat',
        action: 'ChatAjax',
        mode: 'trackNewMessages'
      }).done(({ result }) => {
        this.updateAmountOfNewMessages(result)
        this.initTimer()
      })
    },
    showDialog() {
      setTimeout(_ => {
        if (!this.dragging) {
          this.dialog = !this.dialog
        }
        this.dragging = false
      }, 300)
    }
  },
  created() {
    this.fetchChatConfig().then(result => {
      if (result.config.isChatAllowed && !this.dialog) this.trackNewMessages()
    })
  }
}
